import React,{Component} from 'react'
import { hashHistory} from 'react-router';
import { Form, Input, Button, Radio, Spin, message } from 'antd';
const FormItem = Form.Item;
export class DataForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            "exchangeNumber": "",
            "helpNumber": "",
            "fax": "",
            "address": "",
            "postcode": "",
            "email": ""
        };
        ['handleSubmit', 'resetForm'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        let that = this;
        this.setState({ loading: false});
        ajax({
            url: "/platform/contact/getInfoDetail",
            callback: function(data) {
                if(data.status == 200) {
                    let { exchangeNumber, helpNumber, fax, address, postcode, email } = data.data.detailInfo;
                    that.setState({
                        loading: false,
                        exchangeNumber,
                        helpNumber,
                        fax,
                        address,
                        postcode,
                        email
                    });
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        })
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                ajax({
                    url: "/platform/contact/updateInfo",
                    data: values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("设置成功!", 1);
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        });
    }

    resetForm() {
        this.props.form.resetFields();
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 6 },
            wrapperCol: { span: 6 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="总机电话"
                        hasFeedback
                    >
                        {getFieldDecorator('exchangeNumber', {
                            rules: [
                                { required: true, message: '请输入总机电话!' }
                            ],
                            initialValue: this.state.exchangeNumber
                        })(
                            <Input type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="客服电话"
                        hasFeedback
                    >
                        {getFieldDecorator('helpNumber', {
                            rules: [
                                { required: true, message: '请输入客服电话!' }
                            ],
                            initialValue: this.state.helpNumber
                        })(
                            <Input type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="传真"
                        hasFeedback
                    >
                        {getFieldDecorator('fax', {
                            rules: [
                                { required: true, message: '请输入传真!' }
                            ],
                            initialValue: this.state.fax
                        })(
                            <Input type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="地址"
                        hasFeedback
                    >
                        {getFieldDecorator('address', {
                            rules: [
                                { required: true, message: '请输入客服电话!' }
                            ],
                            initialValue: this.state.address
                        })(
                            <Input type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="邮编"
                        hasFeedback
                    >
                        {getFieldDecorator('postcode', {
                            rules: [
                                { required: true, message: '请输入邮编!' }
                            ],
                            initialValue: this.state.postcode
                        })(
                            <Input type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="邮箱"
                        hasFeedback
                    >
                        {getFieldDecorator('email', {
                            rules: [
                                { required: true, message: '请输入邮箱!' }
                            ],
                            initialValue: this.state.email
                        })(
                            <Input type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        wrapperCol={{ span: 12, offset: 6 }}
                    >
                        <Button type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}>提交</Button>
                        <Button type="default" onClick={this.resetForm}>重置</Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedDataForm = Form.create({})(DataForm);

module.exports = WrappedDataForm;


